import { useEffect, useState } from 'react';
import BackButton from './BackButton/BackButton';
import Day1 from './Days/Day1/Day1';
import DaysList from './DaysList/DaysList';
import HeyScreen from './HeyScreen/HeyScreen';
import './App.css';

let t = null

const screens = [
  {
    title: 'Привіт!',
    emoji: '👋',
    color: '#fff6ec',
  },
  {
    title: 'Як ти?',
    emoji: '👌',
    color: '#fff6ec',
  },
  {
    title: 'Вирішив дещо зробити...',
    emoji: '❓',
    color: '#fff6ec',
  },
  {
    title: 'Я хочу написати тобі 14 листів',
    emoji: '❗️',
    color: '#fff6ec',
  },
  {
    title: 'По одному листу на кожен день',
    emoji: '🤷‍♀️',
    color: '#fff6ec',
  },
  {
    title: 'Не знаю чи зможу',
    emoji: '😨',
    color: '#fff6ec',
  },
  {
    title: 'Але дуже хочу',
    emoji: '💡',
    color: '#fff6ec',
  },
  {
    title: 'Прошу, пані',
    emoji: '👀',
    color: '#fff6ec',
  }
]

const getScreenNumber = () => {
  const storedScreen = localStorage.getItem('screen')
  const screenNumber = storedScreen === null ? -1 : +storedScreen
  // return 0
  return screenNumber
}

const days = [
  Day1,
]

function App() {
  const screenNumber = getScreenNumber()
  const [ screen, setScreen ] = useState(screenNumber)
  const [ showDaysList, setShowDaysList ] = useState(false)
  const [ day, setDay ] = useState(0)
  useEffect(() => {
    if (!t) {
      t = setTimeout(() => {
        screen === -1 && setScreen(0)
        screen === screens.length && setShowDaysList(true)
      }, 1000)
    }
  })
  useEffect(() => {
    localStorage.setItem('screen', screen)
  }, [screen])
  const setScreenFactory = n => () => {
    setScreen(n)
    if (n === screens.length) {
      setShowDaysList(true)
    }
  }
  return (
    <div className="app" style={{ backgroundColor: screens[screen]?.color || '#FFE8D6' }}>
      {screens.map((x, index) => (
        <HeyScreen
          key={x.emoji}
          button={x.emoji}
          show={screen === index}
          onButtonClick={setScreenFactory(index + 1)}>
          {x.title}
        </HeyScreen>
      ))}
      <DaysList show={showDaysList}>
        {days.map((Day, index) => (
          <>
            <button className="day-button" onClick={() => setDay(index + 1)}>Дeнь {index + 1}</button>
            <Day show={day === index + 1} />
          </>
        ))}
        <BackButton onClick={() => setDay(0)} show={day !== 0} />
      </DaysList>
    </div>
  );
}

export default App;
