import React from 'react'
import clsx from 'clsx'
import './HeyScreen.css'

const HeyScreen = ({ show, children, button, onButtonClick }) => {
  return (
    <div className={clsx('hey-screen', show && 'hey-screen--show')}>
      <div className="hey-screen__title">
        {children}
      </div>
      <button className="hey-screen__button" onClick={onButtonClick}>
        <span>{button}</span>
      </button>
    </div>
  )
}

export default HeyScreen
